const renderIntoElement = element =>
  view => element.innerHTML = view;

const renderIntoId = id =>
  renderIntoElement(document.getElementById(id));

const renderIntoSelector = selector =>
  renderIntoElement(document.querySelector(selector));

const intoElement = element =>
  ({ render: renderIntoElement(element) });

const intoId = id =>
  ({ render: renderIntoId(id) });

const intoSelector = selector =>
  ({ render: renderIntoSelector(selector) });

const on = (target, type, callback, useCapture) =>
	target.addEventListener(type, callback, !!useCapture);

const dispatchEvent = (target, selector, handler) => evt => {
  const targetElement = evt.target;
  const potentialElements = target.querySelectorAll(selector);
  const hasMatch = Array.prototype.indexOf.call(potentialElements, targetElement) >= 0;

  if (hasMatch) {
    handler.call(targetElement, evt);
  }
};

const delegate = (target, selector, type, handler) =>
	// https://developer.mozilla.org/en-US/docs/Web/Events/blur
	on(target, type, dispatchEvent(target, selector, handler), type === "blur" || type === "focus");

const meiosisVanillaJs = {
  renderIntoElement,
  renderIntoId,
  renderIntoSelector,
  intoElement,
  intoId,
  intoSelector,
  on,
  delegate
};

export { meiosisVanillaJs };



